@media screen and (max-width: 768px ) {
    #intro h1{
        font-size: 7vw;
    }

    #intro button{
        font-size: 2.3vw;
        width: 25vw;
        height: 9vh;

    }

    #intro h2{
        font-size: 3vw;
        text-align: center;
    }

    .form_field{
        width: 100%;
    }
  
    .direction{
        width: 20vw;
    }

    .progress-bar::before{
        content:"";
        position: absolute;
       
      
        height: 0px;
        width: 0px;
        background-color: rgb(0, 0, 0);
       
        
      }

      form #button_space{
          width: 45vw;
          
      }

      .form_field{
          font-size: 22px;
      }

      #motivation{
          width: 80vw;
          font-size:22px ;
      }
}

