@media screen and (max-width: 431px ) {
    #intro h1{
        margin-top: 19vh;
        font-size: 12vw;
    }

    #intro button{
        font-size: 4vw;
        width: 35vw;
        height: 9vh;

    }

    #intro h2{
        margin-top: 10vh;
        font-size: 4vw;
        text-align: center;
    }

    .form_field{
        width: 100%;
        border-radius: 20px;
    }
  
    .direction{
        margin-top: 3vh;
        width: 40vw;
        height: 8vh;
        font-size: 14px;
        border-radius: 20px;
    }

    .progress-bar::before{
        content:"";
        position: absolute;
       
      
        height: 0px;
        width: 0px;
        background-color: rgb(0, 0, 0);
       
        
      }

    .progress-bar{
        margin-top: 0%;
        
      }

      form #button_space{
          width: 85vw;
          
      }

      .form_field{
          width: 78vw;
          font-size: 16px;
      }

      #motivation{
          padding-top: 10%;
          margin-left: -5vw;
          width: 80vw;
          height: 40vh ;
          font-size:16px ;
          padding-right: 5%;
      }

   
}

